import React from "react";
const sty={
    width: "18rem",
    display:"inline-block",
    backgroundColor:"rgb(56, 232, 211)"
}
export const DisplayItems=({index,contact,deleteContact})=>{return(<>
       <div className="card mx-1 my-1" style={sty}>
        <div className="card-body">
          <h5 className="card-title">First Name={contact.firstName} </h5>
          <p className="card-text">Last Name={contact.lastName}</p>
        </div>
        <ul className="list-group list-group-flush">
          <li className="list-group-item">Office Phone Number={contact.officePhone }</li>
          <li className="list-group-item">Residential Phone Number={contact.residentialPhone}</li>
          <li className="list-group-item">City={contact.city}</li>
          <li className="list-group-item">Country={contact.country}</li>
          <li className="list-group-item">Street Address={contact.streetAddress}</li>
        </ul>
        <div className="card-body">
          <button className="btn-primary btn-danger" onClick={()=>deleteContact(index)}>Delete</button>
        </div>
      </div>
      </>);
}
export const DisplayContacts=({contacts,deleteContact})=>{


    return(<div>
        <div>Total Contacts are {contacts.length}</div>

         {
         contacts.length?contacts.map((contact,index)=>{
             return(<DisplayItems key={index} index={index} contact={contact} deleteContact={deleteContact} />)}):<div>No contacts found</div>
         }
         </div>

     
   
         )
     }