import { useRef, useState } from "react";
import { DisplayContacts,DisplayItems } from "./display";
import { sorting } from "./sorting";
var contacts=JSON.parse(localStorage.getItem('contacts'))?JSON.parse(localStorage.getItem('contacts')):[];
var sty={
    width:"320px"
}

export const Adder=()=>{
  
    function deleteContact(index){
        contacts.splice(index,1);
        localStorage.setItem('contacts',JSON.stringify(contacts));
        setflag(!flag);
    }
    const searchValue = useRef('');
    const firstName = useRef('');
    const lastName = useRef('');
    const officePhone = useRef('');
    const residentialPhone = useRef('');
    const city = useRef('');
    const country = useRef('');
    const streetAddress = useRef('');
    const [flag, setflag] = useState(true);
    const updateflag=()=>{
        setflag(!flag)
    }
    
    const addItems=()=>{
    const data = {
        "firstName": firstName.current.value,
        "lastName": lastName.current.value,
        "officePhone": officePhone.current.value,
        "residentialPhone": residentialPhone.current.value,
        "city": city.current.value, 
        "country": country.current.value,        
        "streetAddress": streetAddress.current.value,
     }
     for(let i=0;i<contacts.length;i++){
         if(firstName.current.value===contacts[i].firstName&&lastName.current.value===contacts[i].lastName){
             alert('Name already exist <Please enter different name>');
             return(0);
         }
     }
     if(firstName.current.value===''){
         alert('Please Enter First Name');
     }else{
    contacts[contacts.length]=data;
    localStorage.setItem('contacts',JSON.stringify(contacts));
    updateflag();
    }
    searchValue.current.value='';
    firstName.current.value='';
    lastName.current.value='';
    officePhone.current.value='';
    residentialPhone.current.value='';
    city.current.value=''; 
    country.current.value="";       
    streetAddress.current.value='';
}
    return(<div>
        <div className="d-flex">
                <input className="form-control me-2" type="Text" placeholder="First Name" ref={firstName}></input>
                <input className="form-control me-2" type="Text" placeholder="Last Name" ref={lastName}></input>
                <input className="form-control me-2" type="Text" placeholder="Office Phone" ref={officePhone}></input>
                <input className="form-control me-2" type="Text" placeholder="Residential Phone" ref={residentialPhone}></input>
                <input className="form-control me-2" type="Text" placeholder="City" ref={city}></input>
                <input className="form-control me-2" type="Text" placeholder="Country" ref={country}></input>                
                <input className="form-control me-2" type="Text" placeholder=" Street Address" ref={streetAddress}></input>
                <button className="btn btn-primary" onClick={addItems}>Add </button>
        
        </div>
        {/* <SearchContacts contacts={contacts}/> */}
        <div>
        <input id='search' className="form-control me-2" type="search" placeholder="Search" aria-label="Search" onKeyUp={updateflag} ref={searchValue}/>
      <button className="btn btn-danger me-2 my-1 p-3"  style={sty}  onClick={()=>{sorting(contacts,'firstName',updateflag)}} >Sort by First Name</button>
      <button className="btn btn-warning me-2 my-1 p-3"  style={sty}  onClick={()=>{sorting(contacts,'lastName',updateflag)}} >Sort by Last Name</button>
      <button className="btn btn-primary me-2 my-1 p-3" style={sty}  onClick={()=>{sorting(contacts,'city',updateflag)}} >Sort by City</button>
      <button className="btn btn-secondary me-2 my-1 p-3"  style={sty} onClick={()=>{sorting(contacts,'officePhone',updateflag)}} >Sort by Office Phone Number</button>
      <button className="btn btn-info me-2 my-1 p-3"  style={sty} onClick={()=>{sorting(contacts,'residentialPhone',updateflag)}} >Sort by Residential Phone Number</button>
      <button className="btn btn-success me-2 my-1 p-3"  style={sty} onClick={()=>{sorting(contacts,'country',updateflag)}} >Sort by Country</button>
      {/* <button className="btn btn- btn-outline-success" type="submit" onClick={()=>{updateflag();}}>Search</button><br/> */}
      {
          (searchValue.current.value===undefined)||( searchValue.current.value==='')?<DisplayContacts contacts={contacts} deleteContact={deleteContact}/>:
         contacts.map((contact, index) => {
                    if (contact.firstName.indexOf(searchValue.current.value)>-1 || contact.lastName.indexOf(searchValue.current.value)>-1 || contact.residentialPhone.indexOf(searchValue.current.value)>-1 || contact.officePhone.indexOf(searchValue.current.value)>-1) {
                    
                      return (<DisplayItems key={index} index={index} contact={contact} deleteContact={deleteContact} />)
                    }})  }
         {/* <DisplayContacts contacts={contacts} deleteContact={deleteContact}/>    */}
        </div>
        </div>
    );  
}
